      | click 和 manual 都是通过点击触发器显示 popover 的，click 模式支持在"第二次点击触发器"和"点击页面上其它区域"时隐藏 popover，
      br
      | manual 模式只能通过手动调用 popover 组件的 close 方法隐藏，popover-edit 组件应用了这种模式
