<script>
import 'bootstrap/dist/css/bootstrap.min.css'
import 'font-awesome/css/font-awesome.css'

export default {
  name: 'app',
  data () {
    return {
      value: ''
    }
  },
  methods: {
    confirm (value, callback) {
      setTimeout(() => {
        this.value = value
        callback()
      }, 500)
    }
